svg {
  width: 80%;
  position: fixed;
  top: 20%;
  left: 10%;
}

section {
  height: 100vh;
  display: grid;
  place-content: center;
  font-family: "Bebas Neue";
  font-size: 5rem;
}

section:nth-of-type(2) {
  background: rgb(232, 227, 214);
}

svg path {
  stroke-dasharray: 0;
  stroke-dashoffset: 0;
  stroke-width: 0.4;
  stroke: #000;
  fill: none;
  transition: stroke-dashoffset 1s;
}
